                        :row-class-name="templateRowStyle"
                        :header-cell-style="{background:'#eef1f6',color:'#606266'}">

                <el-table-column fixed="left" type="selection" ></el-table-column>
                <el-table-column prop="id" label="ID" show-overflow-tooltip></el-table-column>
                <el-table-column prop="template_name" label="" show-overflow-tooltip></el-table-column>
                <el-table-column prop="wl_21r_id" label="21R_ID" show-overflow-tooltip></el-table-column>
                <el-table-column prop="wl_21b_id" label="21B_ID" show-overflow-tooltip></el-table-column>
                <el-table-column prop="note" label="" show-overflow-tooltip></el-table-column>
                <el-table-column label="">
                  <template slot-scope="scope">
                    <i class="el-icon-edit" @click="modifyTemplateClicked(scope.row)"></i>
                  </template>
                </el-table-column>
              </el-table>

              <div class="block">
                <el-pagination
                    @size-change="handleTemplatePaginationSizeChange"
                    @current-change="handleTemplatePaginationCurrentPageChange"
                    :current-page.sync="templatePaginationCurrentPage"
                    :page-sizes="[10, 20, 30, 40, 50, 100]"
                    :page-size="templatePaginationCurrentSize"
                    layout="sizes, prev, pager, next"
                    :total="totalTemplateCount">
                </el-pagination>
              </div>

            </el-tab-pane>
          </el-tabs>



    </el-container>
  </div>
</template>

<script>
import router from "@/router";
const axios = require('axios').default;

export default {
  name: 'Home',
  props: {
    msg: String
  },
  created () {
    // 
    //  data  observed 
    this.fetchData()
  },
  data() {
    return {
      showTable: true,
      showCreateProductDialog: false,
      showCreateTemplateDialog: false,
      showModifyProductDialog: false,
      showModifyTemplateDialog: false,
      showBindLabelDialog: false,
      showProductRelatedButtons: true,
      showTemplateRelatedButtons: false,
      showLabelRelatedButtons: false,
      showProductSwitchTemplateDialog : false,
      label_table_loading: false,
      template_table_loading: false,
      activeName: 'tab_products',
      showPagination: true,
      totalProductCount: 1,
      totalLabelCount: 1,
      totalTemplateCount: 1,
      productPaginationCurrentPage : 1,
      productPaginationCurrentSize: 10,
      templatePaginationCurrentPage : 1,
      templatePaginationCurrentSize: 10,
      labelPaginationCurrentPage : 1,
      labelPaginationCurrentSize: 10,
      labelsToSyncCount: 0,
      userName: window.sessionStorage.getItem('userName'),
      headers :{
        'Authorization': 'Bearer ' + window.sessionStorage.getItem('myToken')
        // // 'Access-Control-Allow-Origin' : '*',
        // 'Access-Control-Allow-Methods' : 'DELETE, POST, GET, OPTIONS',
        // 'Access-Control-Allow-Headers' : 'Content-Type, Access-Control-Allow-Headers, Authorization, X-Requested-With'
      },
      productsData: [],
      labelsData: [],
      templatesData: [],
      product_table_loading:true,
      productsSelection: [],
      labelSelection: [],
      templateSelection: [],
      param_for_querying_labels : {page : 1, size: 1000, storeUuid : '0108'},
      param_for_querying_gateways : {page : 1, size: 1000, storeUuid : '0108'},
      modifyProductForm: {},
      labelBindingForm: {
        barcode: ''
      },
      createProductForm: {
        barcode: '',
        name: '',
        sku: '',
        size: '',
        originalPrice: 0,
        currentPrice: 0,
        templateId: '',
        templateName: '',
        barcodeText: '',
        qrcode: '',
        qrcodeText: '',
        r1: '',
        r2: '',
        r3: '',
        r4: '',
        r5: '',
        r6: '',
        r7: '',
        r8: '',
        r9: '',
        r10: '',
        stock: 0,
        createTime: ''
      },
      modifyTemplateForm: {},
      productSwitchTemplateForm:{
        templateId: ''
      },
      createTemplateForm: {
        template_name: '',
        note: '',
        wl_21r_id: '',
        wl_21b_id: '',
      },
      formLabelWidth: '120px',
      createProductRules: {
        name: [
          {required: true, message: '', trigger: 'blur'}
        ],
        originalPrice: [
          {required: true, message: '', trigger: 'blur'}
        ],
        currentPrice: [
          {required: true, message: '', trigger: 'blur'}
        ],
        templateId: [
          {required: true, message: '', trigger: 'blur'}
        ],
        barcode: [
          {required: true, message: '', trigger: 'blur'}
        ],
        stock: [
          { type: 'number', message: ''}
        ],
      },

      createTemplateRules: {
        template_name: [
          {required: true, message: '', trigger: 'blur'}
        ],
        wl_21b_id: [
          {required: true, message: 'ID', trigger: 'blur'}
        ],
        wl_21r_id: [
          {required: true, message: 'ID', trigger: 'blur'}
        ],
      },

      productSwitchTemplateRules: {
        templateId: [
          {required: true, message: '', trigger: 'blur'}
        ]
      },

      labelBindingRules: {
        barcode: [
          {required: true, message: '', trigger: 'blur'}
        ]
      },

    }
  },

  methods: {

    logout(){
      console.log('logout pressed')
      window.sessionStorage.removeItem('myToken');
      router.push('/login')
    },

    fetchData(){

      this.fetchProducts()
      this.fetchLabels()
      this.fetchTemplates()

    },

    fetchTotalCounts(){
      this.product_table_loading = true
      let self = this

      axios.get('api/hanger/query_total_counts',  {
        headers: this.headers
      }).then((response) => {


        if(response.status != 200){
          self.product_table_loading = false;
          self.product_table_loading = false;
          self.$message.error('');
          console.log('count fetching failed')
          return
        }

        self.totalProductCount = response.data.productCount
        self.totalTemplateCount = response.data.templateCount
        self.totalLabelCount = response.data.labelCount

        console.log(response.data)

      })
    },

    fetchProducts(){
      this.fetchTotalCounts()

      this.product_table_loading = true
      let self = this

      axios.get('api/hanger/product/query', {
        headers: this.headers,
        params: {
          'size' : this.productPaginationCurrentSize,
          'page' : this.productPaginationCurrentPage
        }
      }).then((response) => {

        if(response.status != 200){
          self.product_table_loading = false;
          self.$message.error('');
          console.log('product fetching failed')
          return
        }

        console.log(response)

        self.product_table_loading = false;
        const productList = response.data;

        self.productsData = []
        for (let i = 0; i < productList.length; i++) {
          const productInfo = productList[i]
          self.productsData.push({
            barcode: productInfo.barcode,
            name: productInfo.name,
            sku: productInfo.sku,
            size: productInfo.size,
            originalPrice: productInfo.originalPrice,
            currentPrice: productInfo.currentPrice,
            templateId: productInfo.templateId,
            templateName: productInfo.templateName,
            barcodeText: productInfo.barcodeText,
            qrcode: productInfo.qrcode,
            qrcodeText: productInfo.qrcodeText,
            r1: productInfo.r1,
            r2: productInfo.r2,
            r3: productInfo.r3,
            r4: productInfo.r4,
            r5: productInfo.r5,
            r6: productInfo.r6,
            r7: productInfo.r7,
            r8: productInfo.r8,
            r9: productInfo.r9,
            r10: productInfo.r10,
            stock: productInfo.stock,
            createTime: productInfo.createTime
          })
        }
        //
        // self.reloadTable()
      })
    },

    fetchLabels(){
      this.label_table_loading= true;
      let self = this
      this.labelsToSyncCount = 0;

      axios.get('api/hanger/label/query', {
        headers: this.headers,
        params:{
          page: self.labelPaginationCurrentPage,
          size: self.labelPaginationCurrentSize
        }
      }).then(response =>{
        self.label_table_loading = false

        if(response.status != 200){
          self.$message.error('');
          console.log('label fetching failed')
          return
        }

        const labelList = response.data
        self.labelsData = []

        for (let i = 0; i < labelList.length; i++) {
          const labelInfo = labelList[i]
          let status = ''
          if(labelInfo.product_code == null && labelInfo.action == 1001){
            status = ''
            this.labelsToSyncCount += 1
          }else if(labelInfo.product_code != null &&labelInfo.action == 1001){
            status = ''
            this.labelsToSyncCount += 1
          }else if(labelInfo.action == 1000){
            status = ''
          }else if(labelInfo.action == 0){
            status = ''
          }else if(labelInfo.action == 200){
            status = ''
          }
          let online_status = ''
          if(labelInfo.online == 1){
            online_status = ''
          }else if(labelInfo.online == 0){
            online_status = ''
          }
          self.labelsData.push({
            label_barcode: labelInfo.esl_code,
            product_barcode : labelInfo.product_code,
            spec: labelInfo.esl_version,
            battery: labelInfo.esl_battery,
            ap_name: labelInfo.ap_code,
            online_status: online_status,
            status: status
          })
        }

        this.$refs.labelTable.doLayout();
      })
    },

    fetchTemplates(){
      this.template_table_loading = true;
      let self = this

      axios.get('api/hanger/template/query', {
        headers: this.headers,
      }).then(response =>{
        self.template_table_loading = false

        if(response.status != 200){
          self.$message.error('');
          console.log('template fetching failed')
          return
        }

        const templateList = response.data
        self.templatesData = []
        for (let i = 0; i < templateList.length; i++) {
          const templateInfo = templateList[i]
          self.templatesData.push({
            'id': templateInfo.templateId,
            'template_name': templateInfo.name,
            'wl_21r_id': templateInfo.wl_21r_id,
            'wl_21b_id': templateInfo.wl_21b_id,
            'note': templateInfo.note
          })
        }
      })
    },

    createProductClicked(){
      // this.$refs['createProductDialog'].style.height = '90%';
      this.createProductForm = {
        barcode: '',
        name: '',
        sku: '',
        size: '',
        originalPrice: 0,
        currentPrice: 0,
        templateId: '',
        templateName: '',
        barcodeText: '',
        qrcode: '',
        qrcodeText: '',
        r1: '',
        r2: '',
        r3: '',
        r4: '',
        r5: '',
        r6: '',
        r7: '',
        r8: '',
        r9: '',
        r10: '',
        stock: 0,
        createTime: ''
      }
      this.showCreateProductDialog = true
    },

    createTemplateClicked(){
      this.createTemplateForm = {
        template_name: '',
            note: '',
            wl_21r_id: '',
            wl_21b_id: '',
      },
      this.showCreateTemplateDialog = true
    },

    modifyProductClicked(row){
      this.modifyProductForm = Object.assign({}, row)
      this.showModifyProductDialog = true;
    },

    confirmModifyProduct(){
      axios.post('api/hanger/product/update', this.modifyProductForm, {
        headers: this.headers
      }).then(response =>{
        console.log(response)
        if(response.status == 200 && response.data == true){
          this.$message.success('');
        }else{
          this.$message.error('');
        }
        this.fetchProducts()
        this.showModifyProductDialog = false
      })
    },

    productSwitchTemplateClicked(){
      if(this.productsSelection.length < 1){
        this.$message.error('');
        return
      }

      this.productSwitchTemplateForm.templateId = ''

      this.showProductSwitchTemplateDialog = true
    },

    deleteProductClicked(){
      if(this.productsSelection.length < 1){
        this.$message.error('');
        return
      }

      this.$confirm('', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        let productBarcodes = []
        for (let i = 0; i < this.productsSelection.length; i++) {
          productBarcodes.push(this.productsSelection[i].barcode)
        }

        axios.post('api/hanger/product/remove', {
          barcodes: productBarcodes
        }, {
          headers: this.headers
        }).then((response) =>{
          console.log(response)
          if(response.status == 200 && response.data == true){
            this.$message.success('');
          }else{
            this.$message.error('');
          }
          this.fetchTotalCounts();
          this.fetchProducts()
        })
      })
    },

    deleteTemplateClicked(){
      if(this.templateSelection.length < 1){
        this.$message.error('');
        return
      }

      this.$confirm('', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        let template_ids = []
        for (let i = 0; i < this.templateSelection.length; i++) {
          template_ids.push(this.templateSelection[i].id)
        }

        axios.post('api/hanger/template/remove', {
          templateIds: template_ids
        }, {
          headers: this.headers
        }).then((response) =>{
          console.log(response)
          if(response.status == 200 && response.data == true){
            this.$message.success('');
          }else{
            this.$message.error('');
          }
          this.fetchTotalCounts()

          this.fetchTemplates()
        })
      })
    },

    modifyTemplateClicked(row){
      this.modifyTemplateForm = Object.assign({}, row)
      this.showModifyTemplateDialog = true;
    },

    confirmModifyTemplate(){
      axios.post('api/hanger/template/update', this.modifyTemplateForm, {
        headers: this.headers
      }).then(response =>{
        console.log(response)
        if(response.status == 200 && response.data == true){
          this.$message.success('');
        }else{
          this.$message.error('');
        }
        this.fetchTemplates()
        this.showModifyTemplateDialog = false
      })
    },

    getCurrentDateTime() {
      var now     = new Date();
      var year    = now.getFullYear();
      var month   = now.getMonth()+1;
      var day     = now.getDate();
      var hour    = now.getHours();
      var minute  = now.getMinutes();
      var second  = now.getSeconds();
      if(month.toString().length == 1) {
        month = '0'+month;
      }
      if(day.toString().length == 1) {
        day = '0'+day;
      }
      if(hour.toString().length == 1) {
        hour = '0'+hour;
      }
      if(minute.toString().length == 1) {
        minute = '0'+minute;
      }
      if(second.toString().length == 1) {
        second = '0'+second;
      }
      var dateTime = year+'/'+month+'/'+day+' '+hour+':'+minute+':'+second;
      return dateTime;
    },

    confirmCreateProduct(){
      this.$refs['createProductForm'].validate((valid) => {
        console.log("confirmCreateProduct triggered")
        console.log("valid: " + valid)
        this.createProductForm.currentPrice = parseFloat(this.createProductForm.currentPrice)
        this.createProductForm.originalPrice = parseFloat(this.createProductForm.originalPrice)
        console.log(" " , this.createProductForm.currentPrice)
        console.log(" " , this.createProductForm.originalPrice)
        if(valid){
          this.showCreateProductDialog = false
          this.createProductForm.createTime = this.getCurrentDateTime()
          console.log(this.createProductForm)
          axios.post('api/hanger/product/create', this.createProductForm, {
            headers: this.headers
          }).then(response => {
            console.log(response)
            if(response.status == 200 && response.data == true){
              this.$message.success('');
            }else{
              this.$message.error('');
            }
            this.fetchTotalCounts()
            this.fetchProducts()
          })
        }
      })
    },

    confirmProductSwitchTemplate(){
      this.$refs['productSwitchTemplateForm'].validate((valid) => {
        console.log("confirmProductSwitchTemplate triggered")
        console.log("valid: " + valid)

        if(valid){
          this.showProductSwitchTemplateDialog = false
          console.log(this.productSwitchTemplateForm)

          let barcodes = []
          for (let i = 0; i < this.productsSelection.length; i++) {
            barcodes.push(this.productsSelection[i].barcode)
          }

          axios.post('api/hanger/product/switch_template', {
            barcodes: barcodes,
            templateId: this.productSwitchTemplateForm.templateId
          }, {
            headers: this.headers
          }).then(response => {
            console.log(response)
            if(response.status == 200 && response.data == true){
              this.$message.success('');
            }else{
              this.$message.error('');
            }
            this.fetchProducts()
          })
        }
      })
    },

    confirmCreateTemplate(){
      this.$refs['createTemplateForm'].validate((valid) => {
        console.log("confirmCreateTemplate triggered")
        console.log("valid: " + valid)

        if(valid){
          this.showCreateTemplateDialog = false
          console.log(this.createTemplateForm)
          axios.post('api/hanger/template/create', this.createTemplateForm, {
            headers: this.headers
          }).then(response => {
            console.log(response)
            if(response.status == 200 && response.data == true){
              this.$message.success('');
            }else{
              this.$message.error('');
            }
            this.fetchTotalCounts()
            this.fetchTemplates()
          })
        }
      })
    },

    reloadTable(){
      this.showTable = false
      this.$nextTick(() =>{
        this.showTable = true
      })
    },

    bindLabelClicked(){
      if(this.labelSelection.length < 1){
        this.$message.error('');
        return
      }

      this.labelBindingForm = {
        barcode: ''
      }
      this.showBindLabelDialog = true
    },

    confirmLabelBinding(){
      this.$refs['labelBindingForm'].validate((valid) => {
        console.log("confirmLabelBinding triggered")
        console.log("valid: " + valid)

        if(valid){
          this.showBindLabelDialog = false

          let labelCodes = []
          for (let i = 0; i < this.labelSelection.length; i++) {
            labelCodes.push(this.labelSelection[i].label_barcode)
          }
          axios.post('api/hanger/label/bind', {
            labelCodes: labelCodes,
            productBarcode: this.labelBindingForm.barcode
          }, {
            headers: this.headers
          }).then(response => {
            console.log(response)
            if(response.status == 200 && response.data == true){
              this.$message.success('');
            }else{
              this.$message.error('');
            }
            this.fetchLabels()
          })
        }
      })
    },

    unbindLabelClicked(){
      if(this.labelSelection.length < 1){
        this.$message.error('');
        return
      }

      this.$confirm('', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        let label_ids = []
        for (let i = 0; i < this.labelSelection.length; i++) {
          label_ids.push(this.labelSelection[i].label_barcode)
        }

        axios.post('api/hanger/label/unbind', {
          labelCodes: label_ids
        }, {
          headers: this.headers
        }).then((response) =>{
          console.log(response)
          if(response.status == 200 && response.data == true){
            this.$message.success('');
          }else{
            this.$message.error('');
          }
          this.fetchLabels()
        })
      })
    },

    syncLabelsClicked(){
      axios.post('api/hanger/label/sync_binding', {
      }, {
        headers: this.headers
      }).then((response) =>{
        console.log(response)
        if(response.status == 200 && response.data == true){
          this.$message.success('');
        }else{
          this.$message.error('');
        }
        this.fetchLabels()
      })
    },

    handleProductsSelectionChange(val) {
      this.productsSelection = val;
    },

    handleLabelsSelectionChange(val){
      this.labelSelection = val;
    },

    handleTemplatesSelectionChange(val){
      this.templateSelection = val;
    },

    handleTabChange(tab, event){
      console.log(tab)
      this.showProductRelatedButtons = this.activeName == 'tab_products';
      this.showTemplateRelatedButtons = this.activeName == 'tab_templates';
      this.showLabelRelatedButtons = this.activeName == 'tab_labels';
      if(this.activeName == 'tab_products'){
        this.fetchProducts()
      }else if(this.activeName == 'tab_templates'){
        this.fetchTemplates()
      }else if(this.activeName == 'tab_labels'){
        this.fetchLabels()
      }
    },

    handleProductPaginationSizeChange(val) {
      this.productPaginationCurrentSize = val;
      this.productPaginationCurrentPage = 1;
      console.log(` ${val} `);
      this.fetchProducts()
    },
    handleProductPaginationCurrentPageChange(val) {
      this.productPaginationCurrentPage = val;
      console.log(`: ${val}`);
      this.fetchProducts()
    },

    handleTemplatePaginationSizeChange(val) {
      this.templatePaginationCurrentSize = val;
      this.templatePaginationCurrentPage = 1;
      console.log(` ${val} `);
    },
    handleTemplatePaginationCurrentPageChange(val) {
      this.templatePaginationCurrentPage = val;
      console.log(`: ${val}`);
    },

    handleLabelPaginationSizeChange(val) {
      this.labelPaginationCurrentSize = val;
      this.labelPaginationCurrentPage = 1;
      console.log(` ${val} `);
      this.fetchLabels()
    },
    handleLabelPaginationCurrentPageChange(val) {
      this.labelPaginationCurrentPage = val;
      console.log(`: ${val}`);
      this.fetchLabels()
    },

    productRowStyle({row, rowIndex}) {
      // if (row.connection_status != '') {
      //   console.log(rowIndex)
      //   return {"background-color" : "#e9c8c3"}
      // }
      for (let i = 0; i < this.productsSelection.length; i++) {
        if(row.barcode === this.productsSelection[i].barcode){
          return 'selectedStyle'
        }
      }
    },

    labelRowStyle({row, rowIndex}) {
      for (let i = 0; i < this.labelSelection.length; i++) {
        if(row.label_barcode === this.labelSelection[i].label_barcode){
          return 'selectedStyle'
        }
      }
    },

    templateRowStyle({row, rowIndex}) {
      for (let i = 0; i < this.templateSelection.length; i++) {
        if(row.id === this.templateSelection[i].id){
          return 'selectedStyle'
        }
      }
    },

    resetForm(formName) {
      this.$refs[formName].resetFields();
    }
  }
}


</script>

